import React from 'react';
import { useNavigate } from 'react-router-dom';
import './UserInfo.scss';

function UserInfo() {
  const navigate = useNavigate();
  const gotoUserProfile = () => {
    navigate('/userProfile');
  };
  return (
    <div className="userInfo-wrapper" role="button" tabIndex={0} onClick={gotoUserProfile} onKeyDown={gotoUserProfile}>
      <div className="container">
        <img src="imgs/MyPage/illust_profile_mypage.png" alt=" " className="profile" />
        <div className="name-container">
          <span className="name"></span>
          <img src="imgs/MyPage/ic_arrow_right_mypage.png" alt="  " className="arrow" />
        </div>
        <div className="archiving">  (6)</div>
      </div>
    </div>
  );
}

export default UserInfo;
